<template>
    <div id="container"></div>
    <div id="popup">
        <div class="close-btn"><img src="images/Mother_content_cross.png" /></div>
        <img src="images/Mother_content.jpg" alt="Popup Image" style="width:100%;">
    </div>
</template>

<script>
import { markRaw } from 'vue';
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";
import { RGBELoader } from "three/examples/jsm/loaders/RGBELoader.js";
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';

let model, imageObj;

export default {
    name: "ShopTour",
    data() {
        return {
            isShowPopup: false,
            popupContent: "You've clicked on the 3D object!",
            envMaps: [], // This will store our environment maps
            currentEnvMapIndex: 0, // Track the current environment map
            scene: markRaw(new THREE.Scene()), // Define the scene here to make it accessible
            renderer: null, // Define the renderer here to make it accessible
            controls: null, // Define the controls here to make them accessible
            camera: null, // Define the camera here to make it accessible
            raycaster: null, // Define the raycaster here to make it accessible
            mouse: null, // Define the mouse here to make it accessible
            model1: null, // Define the model here to make it accessible
        };
    },
    methods: {
        async initThreeJS() {
            const container = document.getElementById("container");
            // this.scene = new THREE.Scene();
            this.camera = new THREE.PerspectiveCamera(
                100,
                window.innerWidth / window.innerHeight,
                0.01,
                1000
            );
            this.renderer = new THREE.WebGLRenderer({ antialias: true });
            this.renderer.setSize(container.offsetWidth, container.offsetHeight);
            container.appendChild(this.renderer.domElement);

            this.controls = new OrbitControls(this.camera, this.renderer.domElement);
            this.camera.position.set(0, 0, 5); // Position the camera inside the sphere

            const lookAtPoint = new THREE.Vector3(0, 0, 0);
            const distance = -1 * this.camera.position.distanceTo(lookAtPoint);
            this.camera.position.x = lookAtPoint.x - distance;
            this.camera.position.z = lookAtPoint.z;

            this.camera.lookAt(lookAtPoint);

            this.controls.update();

            // Preload and setup HDR backgrounds
            // await this.loadHDRBackgrounds([
            //     'metro_noord_4k.hdr',
            //     'hospital_room_2_4k.hdr',
            //     'hayloft_4k.hdr',
            // ]);
            await this.loadHDRBackgrounds([
                'room1.hdr',
                'room2.hdr',
                'room3.hdr',
            ]);
            //this.scene.background = this.envMaps[this.currentEnvMapIndex];
            //this.scene.environment = this.envMaps[this.currentEnvMapIndex]; // Set initial environment map

            //console.log(this.envMaps);

            // HDR background setup
            // new RGBELoader() 
            // .setPath('images/') // Update with the path to your HDR file
            // .load('metro_noord_4k.hdr', function (texture) {
            //     texture.mapping = THREE.EquirectangularReflectionMapping;
            //     scene.background = texture;
            //     scene.environment = texture;
            // });

            // Create flashing arrows
            for (let i = 0; i < 2; i++) {
                if (i === 0) {
                    this.createFlashingArrow(this.scene, new THREE.Vector3(-100, -20, 10)); // Middle arrow
                    continue;
                }
                if (i === 1) {
                    this.createFlashingArrow(this.scene, new THREE.Vector3(50, -20, -10)); // Middle arrow
                    continue;
                }
                //this.createFlashingArrow(scene, new THREE.Vector3(i * 5 - 5, 0, 0)); // Adjust positions as needed
            }

            // Add a clickable 3D object
            this.addInteractiveObject(this.scene);

            // Raycaster for click detection
            this.raycaster = new THREE.Raycaster();
            this.mouse = new THREE.Vector2();

            
            //document.addEventListener("mousedown", onDocumentMouseDown, false);

            // Animation loop
            // function animate() {
            //     requestAnimationFrame(animate);
            //     controls.update();
            //     renderer2.render(secne2, camera);
            // }
            this.animate();
        },
        onDocumentMouseDown(event) {
            event.preventDefault();

            this.mouse.x = (event.clientX / window.innerWidth) * 2 - 1;
            this.mouse.y = -(event.clientY / window.innerHeight) * 2 + 1;

            this.raycaster.setFromCamera(this.mouse, this.camera);
            const intersects = this.raycaster.intersectObjects(this.scene.children, true);
            console.log('intersects.length : ' + intersects.length);
            for (let i = 0; i < intersects.length; i++) {
                

                let object = intersects[i].object;
                let tryObj = 5;

                console.log('intersects[i].object.isClickable : ' + object.userData.isClickable);

                // Traverse up to find the root object with userData or until no more parents
                while (object && !object.userData.isClickable && tryObj > 0) {
                    console.log('object.parent : ' + object.parent);
                    console.log('tryObj : ' + tryObj);
                    object = object.parent;
                    tryObj -= 1;
                }

                console.log('intersects[i].object.isClickable2 : ' + object.userData.isClickable);

                if (object.userData.isClickable) {
                    // Handle arrow click - move camera
                    if (object.userData.isArrow) {
                        //const targetPosition = object.position.clone();
                        //targetPosition.z = this.camera.position.z; // Keep original camera Z
                        //moveCameraToPosition(camera, targetPosition);
                        this.changeHDRBackground(i);
                    }
                    // Handle 3D object click - show popup
                    if (object.userData.isInteractiveObject) {
                        console.log('3D object clicked');
                        this.isShowPopup = true;
                        
                        // let screenIdex = 0;
                        // if(this.currentEnvMapIndex < 2) {
                        //     screenIdex = this.currentEnvMapIndex + 1;
                        // }

                        //this.changeHDRBackground(screenIdex);
                        this.showPopup();
                        
                    }

                    if (object.userData.isInteractiveObjectGoToOtherRoom) {
                        console.log('Image object clicked');
                        let screenIdex = 0;
                        if(this.currentEnvMapIndex < 2) {
                            screenIdex = this.currentEnvMapIndex + 1;
                        }

                        this.changeHDRBackground(screenIdex);
                        
                    }
                }
            }
        },
        createFlashingArrow(position) {
            // const arrowGeometry = new THREE.CircleGeometry(10, 32);
            // const arrowMaterial = new THREE.MeshBasicMaterial({
            //     color: 0xffff00,
            //  });
            // const arrow = new THREE.Mesh(arrowGeometry, arrowMaterial);
            // arrow.position.copy(position);
            // arrow.userData = { isClickable: true, isArrow: true };
            // scene.add(arrow);

            // setInterval(() => {
            //     arrow.material.opacity = arrow.material.opacity === 0.5 ? 1.0 : 0.5;
            // }, 3000);
            const tableMaterial = new THREE.MeshLambertMaterial({ color: 0x8B4513 }); // A brown color for tables
            const tableGeometry = new THREE.BoxGeometry(3, 0.2, 3); // Simplified table geometry
            const table = new THREE.Mesh(tableGeometry, tableMaterial);
            table.userData = { isClickable: true, isArrow: true };
            table.position.copy(position);
            this.scene.add(table);
        },
        async loadHDRBackgrounds(hdrPaths) {
            //const loader = new RGBELoader().setDataType(THREE.UnsignedByteType);
            const pmremGenerator = new THREE.PMREMGenerator(this.renderer);
            pmremGenerator.compileEquirectangularShader();

            // Store loaded and processed environment maps
            this.envMaps = []; 

            const promises = hdrPaths.map(path => 
                new Promise((resolve, reject) => {
                    console.log(path);
                    new RGBELoader()
                    .setPath('images/') // Assuming all HDR files are in the 'images/' directory
                    .load(path, function (texture) {
                        texture.mapping = THREE.EquirectangularReflectionMapping;
                        const envMap = texture;
                        //const envMap = pmremGenerator.fromEquirectangular(texture).texture;
                        texture.dispose(); // Free memory resources
                        resolve(envMap);
                    }, undefined, reject);
                })
            );

            Promise.all(promises).then(envMaps => {
                console.log('--------');
                this.envMaps = envMaps;
                console.log(this.envMaps);
                console.log(this.envMaps[0]);
                // Optionally set the first loaded environment map as the initial scene environment
                this.scene.background = this.envMaps[0];
                this.scene.environment = this.envMaps[0];
            }).finally(() => {
                pmremGenerator.dispose(); // Clean up resources
            });
        },
        changeHDRBackground(newIndex) {
            console.log('changeHDRBackground');
            console.log('this.currentEnvMapIndex : ' + this.currentEnvMapIndex);
            console.log('newIndex : ' + newIndex);
            // Smooth transition between HDR backgrounds
            const currentEnvMap = this.envMaps[this.currentEnvMapIndex];
            const newEnvMap = this.envMaps[newIndex];

            
            // Implement a "fade" effect by interpolating the intensity
            let step = 0.0;
            const animateTransition = () => {
                step += 0.05; // Adjust step size for faster or slower transition
                if (step <= 1) {
                    //console.log('step : ' + step);
                    //this.scene.background = THREE.MathUtils.lerp(currentEnvMap, newEnvMap, step);
                    this.scene.environment = THREE.MathUtils.lerp(currentEnvMap, newEnvMap, step);
                    requestAnimationFrame(animateTransition);
                } else {
                    console.log('Finished animation');
                    this.scene.background = newEnvMap;
                    this.scene.environment = newEnvMap; // Ensure we end with the new map
                    this.currentEnvMapIndex = newIndex; // Update current index
                }
            };

            if(newIndex === 0) {
                model.position.set(-5, 0, 0);
                imageObj.position.set(-5, -7, -0.7);
                imageObj.scale.set(2, 1.5, 2);

                this.camera.position.set(0, 0, 5);

                const lookAtPoint = new THREE.Vector3(0, 0, 0);
                const distance = -1 * this.camera.position.distanceTo(lookAtPoint);
                this.camera.position.x = lookAtPoint.x - distance;
                this.camera.position.z = lookAtPoint.z;

                this.camera.lookAt(lookAtPoint);
            } else if(newIndex === 1) {
                model.position.set(30, 0, 0);
                imageObj.position.set(15, -10, 0);
                imageObj.scale.set(2.3, 1.8, 2.3);

                this.camera.position.set(0, 0, 5);
                
                const lookAtPoint = new THREE.Vector3(0, 0, 0);
                const distance = this.camera.position.distanceTo(lookAtPoint);
                this.camera.position.x = lookAtPoint.x - distance;
                this.camera.position.z = lookAtPoint.z;
            } else if(newIndex === 2) {
                model.position.set(10, 0, 0);
                imageObj.position.set(15, -15, -0.7);
                imageObj.scale.set(3, 2.3, 3);

                this.camera.position.set(0, 0, 5);
                
                const lookAtPoint = new THREE.Vector3(0, 0, 0);
                const distance = -1 * this.camera.position.distanceTo(lookAtPoint);
                this.camera.position.x = lookAtPoint.x - distance;
                this.camera.position.z = lookAtPoint.z;

            }

            requestAnimationFrame(animateTransition);
        },
        // moveCameraToPosition(camera, targetPosition) {
        //     const duration = 2000; // Movement duration in milliseconds
        //     const startTime = performance.now();

        //     function animateCamera(time) {
        //     const elapsedTime = time - startTime;
        //     const fraction = elapsedTime / duration;

        //     if (fraction < 1) {
        //         camera.position.lerpVectors(
        //             camera.position,
        //             targetPosition,
        //             fraction
        //         );
        //         requestAnimationFrame(animateCamera);
        //     } else {
        //         // Ensure the final position is set
        //         camera.position.copy(targetPosition);
        //     }
        //     }
        //     requestAnimationFrame(animateCamera);
        // },
        addInteractiveObject() {
            // const geometry = new THREE.BoxGeometry();
            // const material = new THREE.MeshBasicMaterial({ color: 0x00ff00 });
            // const cube = new THREE.Mesh(geometry, material);
            // cube.position.set(0, 0, -5); // Adjust position as needed
            // cube.userData = { isClickable: true, isInteractiveObject: true };
            // scene.add(cube);

            const loader = new GLTFLoader();
            loader.load( 'images/teapot.glb',  ( gltf ) => {
                model = gltf.scene;
                model.position.set(-5, 0, 0);
                model.rotation.y = - 1 * Math.PI / 2;
                model.scale.set(3, 3, 3);
                model.userData = { isClickable: true, isInteractiveObject: true };
                this.scene.add( model );
            }, undefined, function ( error ) {
                console.error( error );
            } );

            // instantiate a loader
            const imgloader = new THREE.TextureLoader();
            imgloader.load('images/Mother_arrow.png', (texture) => {
                // Create a sprite material with the loaded texture
                const material = new THREE.SpriteMaterial({ map: texture, transparent: true });

                // Create the sprite with the material
                imageObj = new THREE.Sprite(material);

                // Set the sprite scale if the image is too big or too small
                imageObj.scale.set(2, 1.5, 2);

                imageObj.userData = { isClickable: true, isInteractiveObjectGoToOtherRoom: true };

                // Position the sprite under your model
                // Assuming your model's position is stored in `model.position`
                // You might need to adjust the y-offset depending on your model's size and the desired effect
                imageObj.position.set(-5, -7, -0.7);

                // Add the sprite to the scene
                this.scene.add(imageObj);
            }, undefined, function(err) {
                console.error('An error occurred loading the arrow texture:', err);
            });
        },
        showPopup() {
            document.getElementById('popup').style.display = 'block';

            //document.querySelector('#popup textarea').focus();

            // Close popup when close button is clicked
            document.querySelector('.close-btn').addEventListener('mousedown', (event) => {
                event.preventDefault();
                event.stopPropagation();

                document.getElementById('popup').style.display = 'none';
                this.isShowPopup = false;
            });
        },
        animate() {
            requestAnimationFrame(this.animate);

            // Update controls
            this.controls.update();

            if (model) {
                const time = Date.now() * 0.001; // Current time in seconds
                model.position.y = Math.sin(time); // Move up and down over time

                // Rotate the model around the Y-axis continuously
                // model.rotation.y += 0.01;
            }

            // Render the scene
            this.renderer.render(this.scene, this.camera);
        }
    },
    mounted() {
        //this.scene = new THREE.Scene();
        this.initThreeJS();
        document.addEventListener('mousedown', this.onDocumentMouseDown.bind(this));
    },
};
</script>

<style>
#container {
  width: 100vw;
  height: 100vh;
}
#popup {
    display: none;
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-color: white;
    border: 1px solid #ddd;
    box-shadow: 0 4px 6px rgba(0,0,0,0.1);
    z-index: 1000;
    pointer-events: auto;
    padding:0;
}

.close-btn {
    cursor: pointer;
    float: right;
    position: absolute;
    top: 10px;
    right: 10px;
}
.close-btn img{
    width:30px;
}
</style>